import { useState, useCallback, useEffect } from "react";
import { Grid, Box, Text } from "grommet";
import { DocumentPdf, Vulnerability, Upload } from "grommet-icons";
import Card from "./components/Card";
import { useDropzone } from "react-dropzone";

const Docs = () => {
  const [files, setFiles] = useState([]);
  const onDrop = useCallback((acceptedFiles) => {
    setFiles(acceptedFiles);
  }, []);

  const { getRootProps, getInputProps, isDragActive } = useDropzone({ onDrop });

  useEffect(() => {
    console.log(files);
    return () => {};
  }, [files]);

  return (
    <>
      <Box pad={{ top: "xlarge" }} />
      <Grid
        {...getRootProps()}
        gap="small"
        columns={{ count: "fit", size: "small" }}
        justify="center"
      >
        {files.length !== 0 ? (
          files.map(({ name, size, type }, i) =>
            type === "application/pdf" ? (
              <Card
                key={i}
                title={name.replace(/\.[^.]+$/, "")}
                subTitle={Math.round(size / 1024) + "k"}
                children={<DocumentPdf />}
                footer=""
                size="small"
              />
            ) : (
              <Box size="xsmall" margin={{ top: "large", left: "medium" }}>
                <Text size="large">#{i}</Text>
                <Text>You can only upload PDF</Text>
              </Box>
            )
          )
        ) : (
          <Box {...getRootProps()} align="center">
            <input {...getInputProps()} />
            {isDragActive ? (
              <Box pad="large">
                <Vulnerability size="large" />
              </Box>
            ) : (
              <Card
                title="Drag 'n' drop files here..."
                subTitle="release when you see the target"
                children={<Upload size="large" />}
                footer=""
                size="large"
              />
            )}
          </Box>
        )}
      </Grid>
    </>
  );
};

export default Docs;
